.container {
  background: #F8FAFC;
  border-radius: 10px;
  padding: 16px;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
}

.title {
  font-size: 18px;
  font-weight: bold;
  color: #000080;
  margin: 0;
}

.appointmentCount {
  font-size: 14px;
  color: #B6B09F;
}

.chartContainer {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  margin-top: 16px;
}

.barsContainer {
  display: flex;
  gap: 24px;
  align-items: flex-end;
}

.barGroup {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.lineGroup {
  display: flex;
  flex-direction: row;
  gap: 4px;
  margin-bottom: 12px;
}

.line {
  width: 2px;
  background-color: #B6B09F;
  border-radius: 2px;
  position: relative;
}

.line1 {
  background-color: #B6B09F;
}

.line2 {
  background: none;
  display: flex;
  flex-direction: column;
  padding: 0;
}

.line2 .top {
  width: 100%;
  background-color: #4ED7F1;
}

.line2 .bottom {
  width: 100%;
  background-color: #211C84;
}

.line3 {
  background-color: #1E90FF;
}

.line4 {
  background: none;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.line4 div {
  width: 100%;
  background-color: #B6B09F;
  border-radius: 2px;
}

.line5 {
  background-color: #211C84;
}

.dayLabel {
  margin-top: 6px;
  font-size: 12px;
  color: #B6B09F;
}